<template>
  <div class="index footer-container">
    <Header title="">
      <template #leftTitle> HPT </template>
    </Header>
    <van-swipe class="index-swipe" indicator-color="white">
      <van-swipe-item
        v-for="(item, index) in dataImage[local].banner"
        :key="index"
        ><img :src="item.image" alt=""
      /></van-swipe-item>
    </van-swipe>
    <div class="price-wrap button1">
      {{ t("index.IDO價格") }} <span>0.05U</span>
    </div>
    <div class="count-down-wrap region-bg">
      <div class="title">{{ t("index.倒計時") }}</div>
      <van-count-down
        :time="time"
        format="DD 天 HH 时 mm 分 ss 秒"
        :auto-start="isStart"
      >
        <template #default="timeData">
          <div class="block">
            <span class="text">{{ filterTimeData(timeData.days) }}</span
            ><span class="name">{{ t("index.天") }}</span>
          </div>
          <div class="colon">:</div>
          <div class="block">
            <span class="text">{{ filterTimeData(timeData.hours) }}</span
            ><span class="name">{{ t("index.時") }}</span>
          </div>
          <div class="colon">:</div>
          <div class="block">
            <span class="text">{{ filterTimeData(timeData.minutes) }}</span
            ><span class="name">{{ t("index.分") }}</span>
          </div>
          <div class="colon">:</div>
          <div class="block">
            <span class="text">{{ filterTimeData(timeData.seconds) }}</span
            ><span class="name">{{ t("index.秒") }}</span>
          </div>
        </template>
      </van-count-down>
      <div class="part">
        {{ IdoInfo._usdtAmount2 || "--" }} U =
        {{ IdoInfo._hptAmount2 || "--" }} HPT
      </div>

      <div class="btn-wrap parent-btn">
        <van-button
          class="btn"
          type="primary"
          @click="$router.push({ name: 'Ido' })"
          >{{ t("index.認購") }}</van-button
        >
      </div>
      <div style="display:none;">
<Ido @IdoInfo="getIdoInfoHandle" @time="timeHandle" ></Ido>
      </div>
      
    </div>
    <div class="calculator">
      <div class="table-title">{{ t("index.優勢亮點") }}</div>
      <div class="cell-2 region-bg" style="margin-bottom: 12px">
        <div class="key">1.{{ t("index.優勢亮點1") }}</div>
        <div class="key">2.{{ t("index.優勢亮點2") }}</div>
        <div class="key">3.{{ t("index.優勢亮點3") }}</div>
        <div class="key">4.{{ t("index.優勢亮點4") }}</div>
        <div class="key">5.{{ t("index.優勢亮點5") }}</div>
        <div class="key">6.{{ t("index.優勢亮點6") }}</div>
        <!-- <div class="value"></div> -->
      </div>
    </div>
    <div class="calculator">
      <div class="table-title">{{ t("index.代幣機制") }}</div>
      <div class="cell-2 region-bg" style="margin-bottom: 12px">
        <div class="key">{{ t("index.代幣機制説明") }}</div>
        <!-- <div class="value"></div> -->
      </div>
    </div>

    <div class="share-wrap">
      <div class="other">
        <a class="item" href="https://twitter.com/Happyplanetcoin" rel="nofollow" target="_blank">
          <img src="@/assets/images/Twitter.webp" alt="" />
        </a>
        <a class="item" href="https://t.me/HappyHPT" rel="nofollow" target="_blank">
          <img src="@/assets/images/Telegram.webp" alt="" />
        </a>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import Ido from "@/components/Ido";
import { reactive, toRefs, computed, watch, ref } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getLang } from "@/utils/i18n";
import { config } from "@/utils/config";
import idoAbi from "@/utils/idoAbi.json";
import tokenAbi from "@/utils/abi";
import { Toast } from "vant";
// import Clipboard from "clipboard";
// import { Toast } from "vant";
export default {
  components: {
    Header,
    Footer,
    Ido,
  },
  setup() {
    const store = useStore();
    const { t } = useI18n();
    const time = ref(0);
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      local: getLang() || "zh-TW",
      idoBtn: {
        loading: false,
      },
      idoContract: null,
      dataImage: {
        "zh-TW": {
          banner: [{ image: require("@/assets/images/banner@2x.png") }],
        },
        en: { banner: [{ image: require("@/assets/images/banner@2x.png") }] },
      },
      IdoInfo: {},
      filterTimeData: (timeData) => {
        return timeData < 10 ? "0" + timeData : timeData;
      },
      initContract: () => {
        data.idoContract = new data.web3.eth.Contract(idoAbi, config.IDO);
        data.usdtContract = new data.web3.eth.Contract(
          tokenAbi,
          config.usdtAddres
        );
        data.initData();
      },
      initData: () => {},
      getIdoInfoHandle: (inject) => {
        data.IdoInfo = inject;
      },
      timeHandle: (inject) => {
        time.value = inject;
      },
    });
    if (data.address) {
      data.initContract();
    }
    watch(
      () => data.address,
      (val) => {
        if (val) {
          data.initContract();
        }
      }
    );
    const refData = toRefs(data);
    return {
      ...refData,
      time,
    };
  },
};
</script>
<style lang="less" scoped>
.footer-container {
  padding: 46px 16px 136px;
  .index-swipe {
    margin: 12.5px 0 29px;
    background: url(~@/assets/images/swiper-bg.webp) no-repeat;
    background-size: 100% 100%;
    .van-swipe-item {
      width: 331px;
      height: 154px;
      img {
        width: 100%;
        height: 100%;
        display: block;
        padding: 8px;
      }
    }
  }
}

.price-wrap {
  font-size: 17px;
  font-weight: 500;
  color: #5bffff;
  text-shadow: 0px 1px 0px rgba(73, 0, 146, 0.35);
  span {
    color: #fff;
  }
  margin: 0 auto 30px;
}
.count-down-wrap {
  padding: 52px 0 29px;
  margin-bottom: 12px;
  position: relative;
  .van-count-down {
    display: flex;
    align-items: center;
    justify-content: center;
    .block {
      width: 55px;
      height: 90px;
      background: url("~@/assets/images/time-bg@2x.png") no-repeat;
      background-size: 100% 100%;
      font-size: 32px;
      font-weight: 600;
      position: relative;
      .text {
        background: linear-gradient(0deg, #b9c1fe 0%, #ffffff 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        position: absolute;
        line-height: 1.1;
        left: 50%;
        transform: translateX(-50%);
        top: 13px;
      }
      .name {
        position: absolute;
        bottom: 3px;
        left: 50%;
        transform: translateX(-50%);
        font-size: 12px;
        color: #b1beea;
      }
    }
    .colon {
      color: #b1beea;
      font-size: 20px;
      font-weight: 600;
      position: relative;
      margin-top: -34px;
      padding: 0 8px;
    }
  }
  .part {
    text-align: center;
    font-size: 15px;
    font-weight: 500;
    color: #00edf8;
    padding: 9px 0;
  }
}
.share-wrap {
  position: fixed;
  bottom: 58px;
  left: 16px;
  right: 22px;
  .other {
    padding: 25px 0;
    display: flex;
    justify-content: flex-end;
    .item {
      text-align: center;
      &:not(:last-child) {
        margin-right: 21px;
      }
      img {
        width: 30px;
        height: 30px;
        display: block;
        margin: 0 auto;
      }
    }
  }
}
</style>
<style lang="less">
#app {
  .index {
    .van-swipe__indicators {
      bottom: 17px;
    }
    .van-swipe__indicator {
      background: url(~@/assets/images/indicator.png) no-repeat;
      background-size: 100% 100%;
      border-radius: initial;
      width: 12px;
      height: 4px;
      background-color: initial !important;
      margin-right: 3px;
    }
    .count-down-wrap {
      .parent-btn {
        margin-top: 3px;
        display: flex;
        justify-content: center;
        .btn {
          width: initial;
          min-width: 113px;
          padding: 0 10px;
          height: 34px;
          line-height: 34px;
        }
      }
    }
  }
}
</style>
<style lang="less" scoped>
.calculator {
  padding-bottom: 16px;
  .cell-2 {
    padding: 20px 30px;
    .key {
      margin-bottom: 10px;
      // padding-top: 24px;
      // padding-bottom: 24px;
      font-size: 15px;
      font-weight: 500;
      color: #00edf8;
      text-align: left;
    }
    .value {
      word-break: break-all;
      padding-top: 11px;
      font-size: 22px;
      font-weight: 600;
      color: #ffffff;
      padding-bottom: 25px;
      span {
        font-size: 14px;
      }
    }
  }
  .input-cell {
    .input-title {
      font-size: 16px;
      font-weight: 500;
