<template>
  <div>
    <div class="btn-wrap parent-btn">
      <van-button v-if="!Number(usdt.allowance)" class="btn" type="primary" :loading="usdt.loading" :disabled="usdt.loading" :loading-text="t('index.參與')+'...'" @click="approveUsdtHandle">{{t('index.參與')}}</van-button>
      <van-button v-else class="btn" type="primary" :loading="dialogBuyPre.loading" :disabled="dialogBuyPre.loading" @click="isDialogBuyHandle" :loading-text="t('index.認購')+'...'">{{t('index.認購')}}</van-button>
    </div>
    <MyDialog v-model:isShow="isDialogPrompt.show" :title="$t('index.提示')" :isSad="!isDialogPrompt.result">
      <template #content>
        <template v-if="isDialogPrompt.result">
          <div class="status">{{t('index.恭喜！')}}</div>
          <div class="info">{{t('index.成功購買')}} <span style="color:#fff;">{{IdoInfo._hptAmount2}}</span> HPT</div>
        </template>
        <template v-else>
          <div class="status">{{t('index.很遺憾！ ')}}</div>
          <div class="info">{{t('index.本次購買失敗！')}}</div>
        </template>
      </template>
      <template #footer>
        <div class="btn-wrap dialog-btn" style="padding-top:0;">
          <van-button type="primary" @click="isDialogPrompt.show = false">{{ $t("index.確定") }}</van-button>
        </div>
      </template>
    </MyDialog>
    <MyDialog v-model:isShow="dialogBuyPre.show" :title="$t('index.購買IDO')" :isShade="false">
      <template #content>
        <div class="dialog-li">
          <div class="key">{{ t("index.支付") }}</div>
          <div class="value theme-color">
            {{IdoInfo._usdtAmount2||'--'}} USDT
          </div>
        </div>
        <div class="dialog-li">
          <div class="key">{{ t("index.獲得") }}</div>
          <div class="value theme-color">
            {{IdoInfo._hptAmount2||'--' }} HPT
          </div>
        </div>
      </template>
      <template #footer>
        <div class="btn-wrap dialog-btn" style="padding-top:0;">
          <van-button type="primary" @click="dialogBuyPre.show = false">{{ $t("index.取消") }}</van-button>
          <van-button type="primary" @click="idoHandle" :loading="idoBtn.loading" :disabled="idoBtn.loading" :loading-text="$t('index.確認') + '...'">{{ $t("index.確認") }}</van-button>
        </div>
      </template>
    </MyDialog>
    <Nft :isRefresh="isRefreshNft" />
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch, getCurrentInstance } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import MyDialog from "@/components/MyDialog";
import Nft from '@/components/Nft'
import BigNumber from "bignumber.js";
import idoAbi from "@/utils/idoAbi.json";
import tokenAbi from "@/utils/abi";
import { config } from "@/utils/config";
import { Toast } from "vant";
import { MaxUint256 } from "@ethersproject/constants";
export default {
  emit: ['IdoInfo', 'time'],
  components: {
    MyDialog,
    Nft
  },
  setup (props, { emit }) {
    const { proxy } = getCurrentInstance();
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      IdoInfo: {},
      idoBtn: {
        loading: false
      },
      usdt: {
        loading: false,
        allowance: 0
      },
      idoContract: null,
      usdtContract: null,
      dialogBuyPre: {
        loading: false,
        show: false
      },
      isDialogPrompt: {
        show: false,
        result: false
      },
      isStart: false,
      isEnd: false,
      isRefreshNft: false,
      initContract: () => {
        data.idoContract = new data.web3.eth.Contract(
          idoAbi,
          config.IDO
        );
        data.usdtContract = new data.web3.eth.Contract(
          tokenAbi,
          config.usdtAddres
        );
        data.initData()
      },
      initData: () => {
        data.getIdoInfoHandle()
        data.getAllowanceUsdt()
      },
      getAllowanceUsdt: async () => {
        let allowance = await data.usdtContract.methods
          .allowance(data.address, config.IDO)
          .call({ from: data.address });
        console.log(allowance, 'getAllowanceUsdt')
        data.usdt.allowance = allowance;
        return allowance;
      },
      approveUsdtHandle: () => {
        data.usdt.loading = true
        data.usdtContract.methods
          .approve(config.IDO, MaxUint256)
          .send({ from: data.address })
          .then(async () => {
            data.usdt.loading = false
            Toast(t("index.批准成功"));
            await data.getAllowanceUsdt();
          })
          .catch((error) => {
            console.log(error)
            data.usdt.loading = false
            Toast(t("index.批准失敗"));
          });
      },
      idoHandle: async () => {
        console.log('idoHandle')
        try {
          data.idoBtn.loading = true
          let _usdtBalance = await data.usdtContract.methods.balanceOf(data.address).call({
            from: data.address
          })
          _usdtBalance = new BigNumber(_usdtBalance)
            .dividedBy(10 ** 18)
            .toFixed();
          if (new BigNumber(_usdtBalance).comparedTo(data.IdoInfo._usdtAmount2) === -1) {
            data.idoBtn.loading = false
            return Toast(t("index.餘額不足"));
          }
          console.log(_usdtBalance, '_usdtBalance', data.IdoInfo._usdtAmount2)
          let _gas = await data.idoContract.methods
            .buy()
            .estimateGas({
              from: data.address
            });
          console.log(_gas, 'buy.estimateGas')
          let res = await data.idoContract.methods.buy().send({
            from: data.address
          });
          console.log(res, 'buy')
          data.dialogBuyPre.show = false // 关闭购买弹窗
          data.idoBtn.loading = false
          data.isDialogPrompt.result = true
          data.isDialogPrompt.show = true // 展示购买结果弹窗
          data.isRefreshNft = !data.isRefreshNft // 查询是否获取nft
        } catch (error) {
          data.dialogBuyPre.show = false // 关闭购买弹窗
          data.isDialogPrompt.result = false
          data.isDialogPrompt.show = true // 展示购买结果弹窗
          data.idoBtn.loading = false
          proxy.$errorformat(error)
        }
      },
      getIdoInfoHandle: async () => {
        const Toast0 = Toast.loading({
          duration: 0,
        });
        try {
          let res = await data.idoContract.methods.getIdoInfo().call({
            from: data.address
          });
          let { _startTime, _endTime, _usdtAmount, _hptAmount } = res
          let _IdoInfo = {
            _startTime, _endTime, _usdtAmount, _hptAmount
          }
          _IdoInfo._usdtAmount2 = new BigNumber(_usdtAmount)
            .dividedBy(10 ** 18)
            .toFixed();
          _IdoInfo._hptAmount2 = new BigNumber(_hptAmount)
            .dividedBy(10 ** 9)
            .toFixed();
            console.log(_IdoInfo);

          data.IdoInfo = _IdoInfo
          emit('IdoInfo', _IdoInfo)
          let curTime = Date.parse(new Date()).toString();
          curTime = curTime.substr(0, 10);
          if (curTime < _startTime) {
            // 未開始
            console.log('未開始', _startTime)
            emit('time', 0)
            data.isStart = false
            data.isEnd = false
          } else if (curTime > _endTime) {
            console.log('已結束')
            emit('time', 0)
            data.isStart = true
            data.isEnd = true
          } else {
            emit('time', (res._endTime - Number(curTime)) * 1000)
            data.isStart = true
            data.isEnd = false
          }
          Toast0.clear()
        } catch (error) {
          Toast0.clear()
          proxy.$errorformat(error)
        }
      },
      isDialogBuyHandle: async () => {
        if (!data.isStart) {
          let startTime = data.IdoInfo._startTime
          return Toast(t("index.未到開始時間：key，請稍後", { key: proxy.$dateformat(startTime) }));
        }
        if (data.isEnd) {
          let endTime = data.IdoInfo._endTime
          return Toast(t("index.已到結束時間：key，請稍後", { key: proxy.$dateformat(endTime) }));
        }
        try {
          data.dialogBuyPre.loading = true
          let res = await data.idoContract.methods.getUserIdo(data.address).call({
            from: data.address
          });
          let { _hptAmount, _usdtAmount } = res
          if (new BigNumber(_hptAmount).comparedTo(0) === 1 && new BigNumber(_usdtAmount).comparedTo(0) === 1) {
            // 大于为1，小于为-1，等于为0
            data.dialogBuyPre.loading = false
            return Toast(t("index.每個地址只能購買一次"));
          } else {
            data.dialogBuyPre.show = true
          }
          data.dialogBuyPre.loading = false
        } catch (error) {
          data.dialogBuyPre.loading = false
          proxy.$errorformat(error)
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
</style>
<style lang="less" scoped>
