<template>
  <div class="my-dialog" v-if="isShow">
    <div class="shade" @click="closeHandle"></div>
    <div class="dialog-wrap">
      <div class="section">
        <div class="dialog anim-scale">
          <slot name="header">
            <div class="header">
              <!-- <img class="dialog-close" @click="$emit('update:isShow',false)" src="@/assets/images/close@2x.png" alt=""> -->
              <div v-if="title" class="dialog-title">{{title}}</div>
            </div>
          </slot>
          <div class="content">
            <slot name="content">
              <div v-if="content">{{content}}</div>
            </slot>
          </div>
          <div class="footer">
            <slot name="footer">

            </slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs, watch } from "vue";
import { useI18n } from "vue-i18n";
export default {
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
    isShade: {
      type: Boolean,
      default: true
    },
  },
  components: {
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      closeHandle: () => {
        if (props.isShade) {
          emit('update:isShow', false)
        }
      }
    })
    watch(
      () => props.isShow,
      (newVal) => {
        if (newVal) {
          document.querySelector('body').style.overflow = 'hidden'
        } else {
          document.querySelector('body').removeAttribute('style')
        }
      }
    )
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.my-dialog {
  position: relative;
  z-index: 2000;
  .shade {
    background-color: rgba(0, 0, 0, 0.5);
    pointer-events: auto;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .dialog-wrap {
    display: table;
    font-family: Helvetica, arial, sans-serif;
    pointer-events: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    .section {
      display: table-cell;
      vertical-align: middle;
      text-align: center;
    }
    .dialog {
      line-height: 1.3;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      background: url(~@/assets/images/dialog-bg2.webp) no-repeat;
      background-size: 100% 100%;
      width: 82% !important;
      border-radius: 0.5 rem;
      border: none;
      color: #fff;
      max-width: 640px;
      position: relative;
      display: inline-block;
      text-align: left;
      font-size: 14px;
      border-radius: 5px;
      pointer-events: auto;
      -webkit-overflow-scrolling: touch;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
      -webkit-animation-duration: 0.2s;
      animation-duration: 0.2s;
      min-height: 368px;
      .header {
        position: relative;
        .dialog-close {
          right: 7px;
          top: 7px;
          position: absolute;
          box-sizing: content-box;
          width: 12px;
          height: 12px;
          padding: 8px;
        }
      }
      .dialog-title {
        color: #5bffff;
        text-align: center;
        padding-top: 93px;
        font-size: 15px;
        font-weight: 600;
        text-shadow: 0px 1px 0px rgba(73, 0, 146, 0.35);
      }
      .content {
        padding: 30px 33px 0;
        text-align: center;
        font-size: 15px;
        color: #fff;
        min-height: 195px;
        word-break: break-all;
      }
    }
  }
  @-webkit-keyframes anim-scale {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }

    100% {
      opacity: 1;
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }

  @keyframes anim-scale {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }

    100% {
      opacity: 1;
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }

  .anim-scale {
    animation-name: anim-scale;
    -webkit-animation-name: anim-scale;
  }
}
</style>
<style lang="less">
.my-dialog {
  .dialog-li {
    display: flex;
    padding-bottom: 21px;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    &:last-child {
      // padding-bottom: 0;
    }
    .input-wrap {
      padding: 0 10px;
      display: flex;
      align-items: center;
      width: 165px;
      .input-label {
        font-size: 15px;
        font-weight: 600;
        color: #fff;
      }
      input {
        height: 37px;
        padding: 0 10px;
        border: 0;
        font-size: 15px;
        width: 100%;
      }
    }
    .input-info {
      width: 100%;
      text-align: right;
      margin-top: 15px;
    }
    .select-wrap {
      select {
        border: 0;
        font-size: 15px;
        color: #fff;
        text-align: right;
        &:focus-visible {
          outline: 0;
        }
      }
    }
    .key {
      font-size: 15px;
      color: #00edf8;
    }
    .value {
      font-size: 15px;
      color: #fff;
      word-break: break-all;
      text-align: left;
      font-weight: 600;
    }
  }
  .dialog-des {
    font-size: 13px;
    text-align: left;
  }
  .status {
    font-size: 16px;
    color: #00edf8;
    padding-top: 4px;
  }
  .info {
    font-size: 16px;
    color: #00edf8;
    padding-top: 4px;
  }
  .dialog-btn {
    margin-top: 0;
    padding: 0px 33px 24px;
    .van-button {
      width: 50%;
      max-width: 131px;
    }
  }
}
</style>
