<template>
  <div class="nft">
    <MyDialog v-model:isShow="isDialogPrompt.show" :title="$t('index.我的NFT盲盒')">
      <template #content>
        <img class="nft-img" src="@/assets/images/nft2.webp" alt="">
      </template>
      <template #footer>
        <div class="btn-wrap dialog-btn" style="padding-top:0;">
          <van-button type="primary" @click="isDialogPrompt.show = false">{{ $t("index.立即領取") }}</van-button>
        </div>
      </template>
    </MyDialog>
    <MyDialog v-model:isShow="isDialogTop.show" :title="$t('index.我的NFT盲盒')">
      <template #content>
        <div class="img-wrap" style="position: relative;">
          <img style="width:204px;height:202px;display:block;margin:0 auto;" src="@/assets/images/top.webp" alt="">
          <div class="top-box">
            <div class="num">{{isDialogTop.num}}</div>
            <div class="label">TOP</div>
          </div>
        </div>
      </template>
    </MyDialog>
    <MyDialog v-model:isShow="isDialogBox.show" :title="$t('index.我的NFT盲盒')">
      <template #content>
        <img class="nft-box" src="@/assets/images/nft3.webp" alt="">
      </template>
      <template #footer>
        <div class="btn-wrap dialog-btn" style="padding-top:0;">
          <van-button type="primary" @click="isDialogBox.show = false">{{ $t("index.稍後拆開") }}</van-button>
          <van-button type="primary" @click="receiveNftHandle">{{ $t("index.立即領取") }}</van-button>
        </div>
      </template>
    </MyDialog>
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch, onBeforeUnmount } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import MyDialog from "@/components/MyDialog2";
import { myBox, luck } from '@/api'
import { useRouter, useRoute } from "vue-router";
import {
  Toast
} from "vant";
export default {
  props: {
    isRefresh: {
      type: Boolean,
      default: false
    }
  },
  components: {
    MyDialog
  },
  setup (props) {
    const route = useRoute()
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      isDialogPrompt: {
        show: false
      },
      isDialogBox: {
        show: false
      },
      isDialogTop: {
        show: false,
        num: '0'
      },
      boxid: '',
      timerInterval: null,
      myBoxHandle: (callback) => {
        myBox({
          address: data.address
        })
          .then((res) => {
            if (res.data.id) {
              data.boxid = res.data.id
              data.isDialogBox.show = true
              window.clearInterval(data.timerInterval);
            }
          })
          .catch(error => {
            console.log(error)
            if (typeof callback === 'function') {
              callback()
            }
          })
      },
      interval: () => {
        window.clearInterval(data.timerInterval);
        data.timerInterval = setInterval(() => {
          data.myBoxHandle()
        }, 3000)
      },
      receiveNftHandle: () => {
        const Toast0 = Toast.loading({
          duration: 0,
        });
        luck({
          boxid: data.boxid,
          address: data.address
        })
          .then((res) => {
            if (res.data.award) {
              window.clearInterval(data.timerInterval);
              data.isDialogBox.show = false
              let award = res.data.award
              if (award.indexOf('NFT') !== -1) {
                data.isDialogPrompt.show = true
              } else if (award.indexOf('TOP') !== -1) {
                let arr = award.split('TOP')
                data.isDialogTop.num = arr[0]
                data.isDialogTop.show = true
              }
            }
            console.log(res, 'receiveNftHandle')
            Toast0.clear()
          })
      }
    })
    onBeforeUnmount(() => {
      window.clearInterval(data.timerInterval);
    })
    console.log(route.name, 'route.name')
    watch(() => props.isRefresh,
      () => {
        data.myBoxHandle(function () {
          if (route.name === 'Nft') {
            Toast(t('index.無待領取盲盒')) // 我的勋章页显示弹窗
          }
          data.interval()
        })
      })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
body {
  .nft-img {
    margin-top: 19px;
    width: 196px;
    height: 127px;
    display: block;
    margin: 0 auto;
    position: relative;
  }

  .nft-box {
    margin-top: 10px;
    width: 128px;
    height: 120px;
    display: block;
    margin: 0 auto;
    position: relative;
  }
  .top-box {
    position: absolute;
    top: 27px;
    left: 50%;
    transform: translateX(-50%);
    width: 50px;
    height: 50px;
    font-size: 15px;
    font-weight: 600;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .label {
      font-size: 12px;
      font-weight: initial;
    }
  }
}
</style>
<style lang="less" scoped>
</style>
